import React, { useEffect, useState } from "react";
import "./App.css";

function App() {
  const url = "https://api.adviceslip.com/advice";

  const [joke, setJoke] = useState("");
  const fetchJokes = async () => {
    const data = await fetch(url);
    const response = await data.json();
    setJoke(response["slip"]["advice"]);
  };

  useEffect(() => {
    fetchJokes();
  }, []);
  return (
    <>
      <div className="card">
        <div className="box">
          {/* <div style={{ fontSize: "45px" }}>ðŸ˜‚</div> */}
          <p className="bold">{joke}</p>

          <div style={{ marginBottom: "20px" }}>
            <button
              type="button"
              className="btn btn-primary"
              onClick={fetchJokes}
            >
              Get Advice
            </button>
          </div>

          <span>
            <ul>
              <li>
                <a
                  href="https://www.facebook.com/akshay.kumar.37051"
                  target="_blank"
                >
                  <i className="fa fa-facebook" aria-hidden="true"></i>
                </a>
              </li>
              <li>
                <a href="https://github.com/TheCodeDecode" target="_blank">
                  <i className="fa fa-github" aria-hidden="true"></i>
                </a>
              </li>

              <li>
                <a
                  href="https://www.linkedin.com/in/akshay-kumar-8b0112108/"
                  target="_blank"
                >
                  <i className="fa fa-linkedin" aria-hidden="true"></i>
                </a>
              </li>
              <li>
                <a href="https://www.instagram.com/akkshhyyy" target="_blank">
                  <i className="fa fa-instagram" aria-hidden="true"></i>
                </a>
              </li>
            </ul>
          </span>
        </div>
      </div>
    </>
  );
}

export default App;
